<template>
    <div class="ai-mobile-search">
        <div class="ai-mobile-search-after">
            <h-icon-search />
        </div>
        <input
            class="ai-mobile-search-input"
            placeholder="ÊêúÁ¥¢"
            :value="props.modelValue"
            @input="updateValue"
        />
        <h-icon-close
            v-if="props.modelValue"
            class="ai-mobile-search-cancel-icon"
            @click="cancelHandle"
        />

        <div
            class="ai-mobile-search-cancel"
            v-if="showCancel || props.modelValue"
            @click="cancelHandle"
        >
            ÂèñÊ∂à
        </div>
    </div>
</template>
<script setup lang="ts">
import { PropType, ref } from 'vue';
import '@hwork/icon/search';

const timer = ref<any>(null);
const props = defineProps({
    modelValue: String as PropType<string>,
    showCancel: {
        type: Boolean as PropType<boolean>,
        default: false,
    },
});

const emit = defineEmits<{
    (e: 'update:modelValue', value: string): void;
    (e: 'cancel'): void;
}>();

const updateValue = (event: Event) => {
    if (timer.value !== null) {
        clearTimeout(timer.value);
    }
    timer.value = setTimeout(() => {
        const target = event.target as HTMLInputElement | null;
        if (target) {
            emit('update:modelValue', target.value);
        }
    }, 300);
};

const cancelHandle = () => {
    emit('update:modelValue', '');
    emit('cancel');
};
</script>
