<template>
    <a-layout class="chat-history">
        <HistoryHeader @delete="clearPrompt" />
        <div class="chat-history-search">
            <MobileSearch v-model="searchText" />
        </div>

        <a-spin :spinning="loading">
            <a-layout-content
                class="chat-list"
                :class="{ 'chat-list-ios': isiOS() }"
                v-if="todayChatGroupList.length || historyChatGroupList.length"
            >
                <div v-if="todayChatGroupList.length > 0" class="date-wrap">
                    ‰ªäÂ§©
                </div>
                <template
                    v-for="item in todayChatGroupList"
                    :key="item.groupCode"
                >
                    <ChatCard
                        :chatGroup="item"
                        @jump="changeChatGroup(item.groupCode)"
                        @delete="clearPrompt(item.groupCode)"
                    />
                </template>
                <div
                    v-if="historyChatGroupList.length > 0"
                    class="date-wrap mt20"
                >
                    ÊúÄËøë7Â§©
                </div>
                <template
                    v-for="item in historyChatGroupList"
                    :key="item.groupCode"
                >
                    <ChatCard
                        :chatGroup="item"
                        @jump="changeChatGroup(item.groupCode)"
                        @delete="clearPrompt(item.groupCode)"
                    />
                </template>
            </a-layout-content>
            <div class="chat-list-empty" v-else>
                <img :src="emptyDataImage" />
                <span>
                    {{ searchText ? 'Êó†ÊêúÁ¥¢ÁªìÊûú' : 'ÊöÇÊó†Êï∞ÊçÆ' }}
                </span>
            </div>
        </a-spin>
    </a-layout>
    <ClearHistoryModal
        ref="modalRef"
        @refresh="() => queryChatGroupList(searchText)"
    />
</template>
<script setup lang="ts">
import { ref, computed, watch } from 'vue';
import dayjs from 'dayjs';
import ChatCard from '@/components/ChatCard/index.vue';
import HistoryHeader from '@/components/HistoryHeader.vue';
import ClearHistoryModal from '@/components/ClearHistoryModal/ClearHistoryModal.vue';
import MobileSearch from '@/components/MobileSearch/index.vue';
import emptyDataImage from '@/assets/images/empty-data.png';
import {
    chatGroupList,
    queryChatGroupList,
    changeChatGroup,
    chatGroupFilterList,
} from '@/hooks/useChatGroupList';
import { isiOS } from '@/utils/util';
const searchText = ref<string>('');
const modalRef = ref<InstanceType<typeof ClearHistoryModal>>();
const loading = ref<boolean>(true);
const todayChatGroupList = computed(() => {
    let list = chatGroupList.value;
    if (searchText.value) {
        list = chatGroupFilterList.value;
    }

    const tmp = list.filter((item) => {
        return (
            dayjs(item.lastChatTime).format('YYYY-MM-DD') ===
                dayjs().format('YYYY-MM-DD') && item.agentCode !== 'feedback'
        );
    });
    return tmp.reverse();
});

const historyChatGroupList = computed(() => {
    let list = chatGroupList.value;
    if (searchText.value) {
        list = chatGroupFilterList.value;
    }
    const tmp = list.filter((item) => {
        return (
            dayjs(item.lastChatTime).format('YYYY-MM-DD') !==
            dayjs().format('YYYY-MM-DD')
        );
    });
    return tmp.reverse();
});
const clearPrompt = (groupCode?: string) => {
    modalRef.value?.show({ groupCode });
};

watch(
    () => searchText.value,
    async () => {
        loading.value = true;
        await queryChatGroupList(searchText.value);
        loading.value = false;
    },
    { immediate: true },
);
</script>
