    <a-modal
        v-model:visible="modalVisible"
        :closable="false"
        :footer="null"
        :width="311"
        wrapClassName="clear-modal-mobile"
    >
        <div class="modal-content">Âà†Èô§ÂêéÊó†Ê≥ïÊÅ¢Â§çÔºåÊòØÂê¶ÁªßÁª≠Ê≠§Êìç‰ΩúÔºü</div>
        <div class="footer-wrap">
            <div class="btn" @click="modalVisible = false">ÂèñÊ∂à</div>
            <div class="btn danger" @click="handleClear">Á°ÆËÆ§</div>
        </div>
    </a-modal>
</template>
<script setup lang="ts">
import { ref } from 'vue';
import { useRouter } from 'vue-router';
import { clearChatGroups } from '@/hooks/useChatGroupList';
import { deleteChatGroup } from '@/api/chat';
const router = useRouter();
const modalVisible = ref(false);
const emit = defineEmits<{
    (e: 'refresh'): void;
}>();
async function handleClear() {
    await clearChatGroups();
    router.push({
        path: '/chat/new',
        query: {
            agent: 'super',
        },
    });
    modalVisible.value = false;
};
async function deleteHistory(groupCode: string) {
    await deleteChatGroup(groupCode);
};
const show = async ({ groupCode }: { groupCode?: string }) => {
    if (groupCode) {
        await deleteHistory(groupCode);
        emit('refresh');
    } else {
        modalVisible.value = true;
    }
};
defineExpose({ show });
</script>
